import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { SET_DATA } from 'store/actions';

// material-ui
import { Grid, Table, TableContainer, TableBody, TableHead, TableRow, TableCell, Typography, Slider, TextField } from '@material-ui/core';
import StockChartCard from 'views/dashboard/Default/StockChartCard';
import MainCard from 'ui-component/cards/MainCard';
// project imports
import { gridSpacing } from 'store/constant';

import StockCard from 'views/dashboard/Default/StockCard';
import SubCard from 'ui-component/cards/SubCard'
import configData from 'config';
import { makeStyles, useTheme } from '@material-ui/styles';

import axios from 'axios';

//==============================|| TYPOGRAPHY ||==============================//

const Prediction = () => {
    const [isLoading, setLoading] = useState(true);
    const [dataset, setDataset] = useState([]);
    const [best, setBest] = useState([]);
    const [stats, setStats] = useState({});
    const account = useSelector((state) => state.account);
    
    const [value, setValue]  = useState([60, 30, 10]);
    const [tempValue, setTempValue]  = useState([60, 30, 10]);
    const theme = useTheme();

    const committed = () => {

        setValue(tempValue);
    }

    const handleChange = (t, index) => {
        let temp = [...tempValue];
        temp[index] = t;

        const leftover = 100 - t;
        const denom = temp.reduce((partialSum, a) => partialSum + a, 0) - t;
        let scale = 0;
        if (denom !== 0){
            scale = leftover/denom;
        }
        
        for (let i = 0; i<value.length; i++){
            if (i!==index){
                temp[i] = scale*temp[i];
            }
        }
        setTempValue(temp);
    }

    useEffect(() => {
        const url = configData.API_SERVER+"stats?allocation="+encodeURIComponent(value);
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => {
                setDataset(response.data.returns);
                if (isLoading) setBest(response.data.best);
                setStats(response.data.stats);
            })
          .then(() => setLoading(false))
      }, [value]);


    return (
            <Grid container spacing={gridSpacing} justifyContent="center">
                {dataset.length === 0 || best.length === 0 ? 
                <Typography variant="h1"></Typography> :
                <Grid container item spacing={gridSpacing} justifyContent="center">
                    <Grid item xs={12} lg={6}>
                    <SubCard>
                        <Grid item xs={12} sx={{mb: 4}}>
                            <Typography variant="h1" align="center">
                                Portfolio Builder
                            </Typography>
                            <Typography variant="subtitle1" align="center">
                                Building your portfolio using multiple strategies
                            </Typography>
                        </Grid>
                        <Grid container spacing={gridSpacing} justifyContent="center">
                        <Grid container item xs={10} >
                            <Grid item xs={12} md={4}>
                                <Typography variant="h3">
                                    { best[0][0] }
                                </Typography>
                            </Grid>
                            <Grid item md={8} xs={12}>
                                <Slider
                                    getAriaLabel={() => 'Strategy 0'}
                                    value={tempValue[0]}
                                    onChange={(t) => handleChange(t.target.value, 0)}
                                    onChangeCommitted={committed}
                                    valueLabelDisplay="auto"
                                    disableSwap
                                    />
                            </Grid>
                        </Grid>
                        
                        <Grid container item xs={10}>
                            <Grid item xs={12} md={4}>
                                <Typography variant="h3">
                                    { best[1][0] }
                                </Typography>
                            </Grid>
                            <Grid item md={8} xs={12}>
                                <Slider
                                    getAriaLabel={() => 'Strategy 1'}
                                    value={tempValue[1]}
                                    onChange={(t) => handleChange(t.target.value, 1)}
                                    onChangeCommitted={committed}
                                    valueLabelDisplay="auto"
                                    disableSwap
                                    />
                            </Grid>
                        </Grid>

                        <Grid container item xs={10}>
                            <Grid item xs={12} md={4}>
                                <Typography variant="h3">
                                    { best[2][0] }
                                </Typography>
                            </Grid>
                            <Grid item md={8} xs={12}>
                                <Slider
                                    getAriaLabel={() => 'Strategy 2'}
                                    value={tempValue[2]}
                                    onChange={(t) => handleChange(t.target.value, 2)}
                                    onChangeCommitted={committed}
                                    valueLabelDisplay="auto"
                                    disableSwap
                                    />
                            </Grid>
                        </Grid>
                        </Grid>
                        <Grid container spacing={gridSpacing} sx={{ mt: 2 }}>
                                <Grid item xs={12} md={4} justifyContent="center" align="center">
                                    <Typography display="inline" variant="h1" sx={{ fontWeight: 'regular'}}>
                                        {stats.score ? stats.score: 0} 
                                    </Typography>
                                    <Typography display="inline" variant="h4">/5</Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Risk Score
                                    </Typography>
                                </Grid>
                                <Grid item xs={12} md={4} justifyContent="center" align="center">
                                    <Typography variant="h1" sx={{ fontWeight: 'regular' }}>
                                        {stats.mdd ? stats.mdd: 0}%
                                    </Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Max Drawdown
                                    </Typography>
                                </Grid>
                                <Grid item xs={12} md={4} justifyContent="center" align="center">
                                    <Typography variant="h1" sx={{ fontWeight: 'regular' }}>
                                        {stats.annualized_return ? stats.annualized_return: 0}%
                                    </Typography>
                                    <Typography variant="h2" sx={{ color: theme.palette.secondary.dark }}>
                                        Annualized Return
                                    </Typography>
                                </Grid>
                            </Grid>
                        </SubCard>
                    </Grid>
                <Grid item lg={6} xs={12} justifyContent="center" align="center">
                    <MainCard>
                        {/* <Typography> */}
                            <Typography variant="h1" align="center">
                                Performance vs. Benchmark
                            </Typography>
                            <StockChartCard white={true} dataset={dataset[0]} benchmark={dataset[1]}/>
                        {/* </Typography> */}
                    </MainCard>
                </Grid>
                
            </Grid> }
                

                {/* {dataset.map(item => (
                    <Grid item key={item.name} xs={12} md={6} lg={4}>
                        <StockCard isLoading={isLoading} data={item}/>
                    </Grid>
                ))} */}
            </Grid>
    );
};

export default Prediction;
